
import { defineComponent } from 'vue';

export default defineComponent({
  name: "PinString",

  props: {
    pinLength: {
      type: Number,
      required: true
    },

    value: {
      type: String,
      default: ''
    },

    isLoading: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    charPositions() {
      const positions = [];
      let i = 0;

      for (i; i < this.pinLength; i++) {
        positions.push(i);
      }
      return positions;
    }
  }
});
